  }
}
@media (max-width: 400px) {
  .todo__list__items {
    grid-template-columns: 200px;
    margin-right: 15px;
  }
}
@media (max-width: 370px) {
  .todo__list__items {
    grid-template-columns: 180px;
  }
}
@media (max-width: 345px) {
  .todo__list__items {
    grid-template-columns: 155px;
  }
}
